<template>
    <p-data-table
        :value="semenList"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        dataKey="semenId"
        v-model:selection="selectedSemen"
        selectionMode="single"
        @row-select="onRowSelect"
        @row-dblclick="redirectToSemen"
    >
        <p-column-group type="header">
            <p-row>
                <p-column header="" colspan="6" />
                <p-column header="Semen" colspan="8" />
                <p-column header="Wash" colspan="6" />
                <p-column header="" colspan="2" />
            </p-row>
            <p-row>
                <p-column
                    headerStyle="width: 3em; max-width: 3rem"
                    style="width: 3em; max-width: 3rem"
                >
                    <template #header>
                        <p-checkbox
                            v-model="selectAll"
                            :binary="true"
                            @change="selectAllOnChange"
                        />
                    </template>
                </p-column>

                <p-column
                    header="Semen ID"
                    headerStyle="width: 8em; max-width: 8rem;"
                />
                <p-column
                    header="Type"
                    headerStyle="width: 5em; max-width: 5rem;"
                />
                <p-column
                    header="Date"
                    headerStyle="width: 7em; max-width: 7rem;"
                />
                <p-column
                    header="Patient Name"
                    headerStyle="width: 8em; max-width: 8rem;"
                />
                <p-column
                    header="Chart"
                    headerStyle="width: 5em; max-width: 5rem;"
                />

                <p-column
                    header="Vol"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 3rem;"
                />
                <p-column
                    header="Conc"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />
                <p-column
                    header="Total"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />
                <p-column
                    header="Mot"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />
                <p-column
                    header="TMC"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />

                <p-column
                    header="PUS"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />
                <p-column
                    header="DNA"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />
                <p-column
                    header="Mor"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />

                <p-column
                    header="Vol"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 3rem;"
                />
                <p-column
                    header="Conc"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />
                <p-column
                    header="Total"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />
                <p-column
                    header="Mot"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />
                <p-column
                    header="TMC"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />

                <p-column
                    header="PUS"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />

                <p-column
                    header="Donor"
                    colspan="1"
                    headerStyle="width: 5em; max-width: 4rem;"
                />

                <p-column header="Comment" />
            </p-row>
        </p-column-group>

        <p-column
            headerStyle="width: 3em; max-width: 3rem"
            style="width: 3em; max-width: 3rem"
        >
            <template #body="{data}">
                <p-checkbox
                    v-model="checkList"
                    :name="data.semenId"
                    :value="data.semenId"
                />
            </template>
        </p-column>

        <p-column field="semBarcode" />
        <!-- <p-column field="type" /> -->

        <p-column>
            <template #body="{data}">
                <span
                    :style="{
                        'background-color':
                            data.type === 'wash' ? '#50aaf3' : 'defaultColor'
                    }"
                    class="type-span"
                >
                    {{ data.type }}
                </span>
            </template>
        </p-column>

        <p-column field="semenDate" />
        <p-column field="patientName" />
        <p-column field="patientId" />

        <p-column field="volume" />
        <p-column field="concentration" />
        <p-column field="totalCount" />
        <p-column field="motility" />
        <p-column field="totalMotility" />

        <p-column field="roundCells" />
        <p-column field="fragmentation" />
        <p-column field="morphNormal" />
        <p-column field="volume1" />
        <p-column field="concentration1" />
        <p-column field="totalCount1" />
        <p-column field="motility1" />
        <p-column field="totalMotility1" />

        <p-column field="roundCells1" />

        <p-column field="spermDonor">
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.spermDonor"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>

        <p-column field="comment" style="min-width: 280px;" />

        <template #footer>
            <div class="footer-buttons">
                <p-button
                    label="Create"
                    class="p-mr-2 p-button-success full-width-button"
                    @click="create"
                    v-if="hasCreatePermission"
                />
                <p-button
                    label="Edit"
                    class="p-mr-2 full-width-button"
                    @click="edit"
                    :disabled="selectedSemen == null"
                    v-if="hasUpdatePermission"
                />
                <p-button
                    label="Delete"
                    class="p-mr-2 p-button-danger full-width-button"
                    @click="deleteSemenConfirm"
                    :disabled="selectedSemen == null"
                    v-if="hasDeletePermission"
                />

                <p-button
                    label="PDF Reports"
                    class="p-mr-2 p-button-secondary full-width-button"
                    @click="openPdfReportStandard"
                />
            </div>
        </template>
    </p-data-table>

    <semen-create-edit-dailog :type="dialog.type" :display="dialog.display" />

    <p-dialog
        v-model:visible="deleteDialogvisible"
        modal
        header="Delete Semen!!"
        :style="{ width: '30rem' }"
    >
        <p>
            Are you sure you want to delete?
        </p>
        <p-button label="No" class="p-mr-2" @click="cancelDeleteSemen" />
        <p-button label="Yes" class="p-button-danger" @click="deleteSemen" />
    </p-dialog>
</template>

<script>
import { onMounted, provide } from "vue";
import { useDialog, useSemenSetup } from "@/composable/semen";

import SemenCreateEditDailog from "@/components/semen/create-edit-dialog.vue";
import { usePermissionCheck } from "@/composable/users";
import { useRouter } from "vue-router";
import { ref, watch } from "vue";

export default {
    components: {
        SemenCreateEditDailog
    },
    setup() {
        const router = useRouter();
        const {
            store,
            toast,
            semenList,
            checkList,
            selectAll,
            selectAllOnChange,
            selectedSemen,
            onRowSelect,
            setSelectedItemNull,
            getSemenList,
            deleteSemen,
            openPdfReport,
            openPdfReportStandard,
            deleteDialogvisible
        } = useSemenSetup();

        const { dialog, create, edit, close } = useDialog();
        provide("dialog", dialog);

        const {
            hasUpdatePermission,
            hasDeletePermission,
            hasCreatePermission
        } = usePermissionCheck();

        // watch(cyclesSummary, (newValue) => {
        //     checkList.value = [];
        // });

        const cancelDeleteSemen = () => {
            deleteDialogvisible.value = false;
        };

        const deleteSemenConfirm = () => {
            deleteDialogvisible.value = true;
            // deleteCycleNo.value = null;
        };

        const redirectToSemen = (data) => {  
            
            // console.log(data);
            let queryParam = null;

            queryParam = {
                patientId: data.data.patientId,
                to: "semen"
            };

            if (queryParam != null) {
                router.push({
                    name: "redirect",
                    query: queryParam
                });
            }
        };

        onMounted(() => {
            setSelectedItemNull();

            getSemenList(
                store.state.artCycle.patient.id,
                store.state.artCycle.partner.id != null
                    ? store.state.artCycle.partner.id
                    : 0
            );
        });

        return {
            deleteDialogvisible,
            deleteSemenConfirm,
            cancelDeleteSemen,
            checkList,
            selectAll,
            selectAllOnChange,
            semenList,
            selectedSemen,
            onRowSelect,
            setSelectedItemNull,
            dialog,
            create,
            edit,
            close,
            deleteSemen,
            openPdfReport,
            hasUpdatePermission,
            hasDeletePermission,
            hasCreatePermission,
            openPdfReportStandard,
            redirectToSemen
        };
    }
};
</script>

<style scoped>
.type-span {
    display: inline-block;
    padding: 5px;
    font-size: 16px; 
    color: #000000;
    border-radius: 4px;
}
@media (max-width: 768px) {
    .footer-buttons {
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin-right: 0 !important;
    }

    .full-width-button {
        width: 100%;
        text-align: center;
    }
}
</style>
