<script>
import { onMounted, provide, ref } from "vue";
import { useDialogSetup } from "@/composable/semen";

import SemenFateTable from "@/components/semen/fate-table.vue";
export default {
    components: {
        SemenFateTable
    },
    setup() {
        const {
            store,
            patient,
            partner,
            parnterFiltered,
            searchParnter,
            onParnterSelect,
            partnerDOB,
            dialog,
            form,
            loading,
            onShow,
            create,
            update,
            v$,
            methodOfCollectionOpts,
            daysOfAbstinenceOpts,
            liquifiedOpts,
            viscosityOpts,
            phOpts,
            progressionOpts,
            agglutinationOpts,
            embryologistOpts
        } = useDialogSetup();

        const createSemenType = () => {
            if (v$.value.$invalid) return;
            if (form.type == "" || form.type == null) {
                toast.add({
                    severity: "warn",
                    summary: "Error",
                    detail: "Semen Type is required",
                    life: 3000
                });

                return;
            }
        };

        const donoroptions = [
            { name: "Donor", key: "semen-donor" },
            { name: "Non-Donor", key: "semen-non-donor" }
        ];

        const typeOptions = ref([
            { label: "Semen", value: "semen" },
            { label: "Wash", value: "wash" }
        ]);

        onMounted(() => {
            loading.value = false;
        });

        return {
            typeOptions,
