<template>
    <p-data-table :value="fates" class="p-datatable-sm p-my-2" showGridlines>
        <p-column field="fate" header="Fate" />
        <p-column field="vialId" header="Vial ID" />
        <p-column field="freezeDate" header="Freeze Date" />
        <p-column field="thawDate" header="Thaw Date" />
    </p-data-table>

    <p-button label="Create" class="p-mr-1" @click="showCreateFateDailog" />
    <p-button label="Load Data" />

    <semen-create-fate-dailog />
</template>

<script>
import { provide, ref } from "vue";
import SemenCreateFateDailog from "@/components/semen/create-fate-dialog.vue";

export default {
    components: {
        SemenCreateFateDailog
    },
    setup() {
        const fates = ref([
            {
                fate: 1,
                vialId: 2,
                freezeDate: "2022-01-02",
                thawDate: "2022-01-02"
            }
        ]);

        const createFateDailog = ref(false);
        const showCreateFateDailog = () => {
            createFateDailog.value = true;
        };

        provide("createFateDailog", createFateDailog);

        return { fates, createFateDailog, showCreateFateDailog };
    }
};
</script>
